import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';

const useStyles = makeStyles(theme => ({
  footer: {
    position: 'absolute',
    width: '100%',
    bottom: 0,
    height: '3rem',
    background: `rgba(127,177,217,1)`,
    background: `linear-gradient(90deg, rgba(0,104,155,1) 0%, rgba(36,140,199,1) 35%, rgba(127,177,217,1) 100%)`, 
  },
  author: {
    fontFamily: 'Inconsolata',
    textAlign: 'center',
    marginTop: '0.2rem',
    color: 'white',
    [theme.breakpoints.up("sm")]: {
      marginTop: 0,
      lineHeight: '3rem',
    }
    
  },
  date: {
    bottom: 0,
    left: '5px',
    position: 'absolute',
    fontWeight: 'bold',
    fontFamily: 'Ubuntu',
    color: 'white'
  },
}));

export default function AveryFooter() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <footer className={classes.footer}>
        <Container maxWidth="m">
          <Typography className={classes.author} variant="body1">Made by jaack.dev</Typography>
          <Typography className={classes.date} variant="body2">
              {'Copyright Â© '}
              <Link color="inherit" href="https://www.teamaveryessex.com">
                Team Avery Essex
              </Link>{' '}
              {new Date().getFullYear()}
              {'.'}
          </Typography>
        </Container>
      </footer>
    </div>
  );
}