import React from 'react';
import {makeStyles} from '@material-ui/core/styles'
import { Paper, Grid, Typography, CardMedia, Card, CardContent} from '@material-ui/core';
import AstonsWayForm from './forms/AstonsWayForm'
import {useMediaQuery} from 'react-responsive'
import AveryHero from './AveryHero'
import Woodlands from './static/AstonsWay/woodlands.jfif'
import Police from './static/AstonsWay/police.jfif'


const useStyles = makeStyles(theme => ({
    root: {

    },
    pamperText: {
        fontFamily: 'Rubik',
        fontSize: '1.2rem',
        margin: '0px 20px 0px 20px'
    },
    card: {
        boxShadow: '0 6px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
    },
    cardMedia: {
    },
    cardCaption : {
        fontSize: '1.1rem',
        textAlign: 'center',
        fontFamily: 'Ubuntu',
    },
    formNote: { 
        textAlign: 'center',
        margin: '0px 20px 0px 20px',
        fontFamily: 'Ubuntu',
    }
}))

function AstonsWay() {
    const classes = useStyles();

    const isLarge = useMediaQuery({query: "(min-width: 1200px)"})
    const isSmall = useMediaQuery({query: "(min-width: 400px) and (max-width: 600px)"})
    let gridSize = 5
    let cardHeight = undefined
    if (isLarge) {gridSize = 4}
    if (isSmall) {cardHeight = 350}

    return (
        <div>
            <AveryHero
            primary="Aston's Way"
            secondary="Involving and educating people on autism."
            />
            <Grid 
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <Grid item xs={10}>
                    <div data-aos="fade-up">
                        <Grid
                        container
                        spacing={3}
                        alignItems="stretch"
                        justify="center"
                        >
                            <Grid item sm={gridSize}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="Aston's Way at Woodlands School"
                                    height={cardHeight}
                                    image={Woodlands}
                                    title="Aston's Way at Woodlands School"
                                    /> 
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            Aston's Way at Woodlands School.
                                        </Typography>
                                    </CardContent>
                                </Card> 
                            </Grid>
                            <Grid item sm={gridSize}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="Aston's Way with the emergency services"
                                    height={cardHeight}
                                    image={Police}
                                    title="Aston's Way with the emergency services"
                                    />
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            Aston with the emergency services.
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        </Grid>
                        <br/>
                        <Paper>
                        <br/>
                            <Typography className={classes.pamperText}>
                                Aston's Way (in collaboration with <a href="https://www.training2care.co.uk/" rel="noopener noreferrer">Training 2 Care</a>)
                                is an innovative new venture by Team Avery Essex and Training 2 Care, a company providing high quality training to groups and businesses. We've partnered with Training 2 Care to use their 
                                state of the art autism reality experience buses to help educate schools and groups on autism. The buses provide a very eye-opening experience of living with autism, introducing some of the challenges 
                                such as sensory overload. Aston then supplements this experience with talks and presentations on the topic.<br/><br/> We've previously partnered with <a href="http://www.essex-fire.gov.uk/" rel="noopener noreferrer">Essex Police and Fire Service</a>, and&nbsp;
                                <a href="http://woodlandsschool.org/" rel="noopener noreferrer">Woodlands School</a> to provide training days and experiences for the emergency services and students. These days were incredibly well received and all attendees felt they learnt 
                                valuable lessons on autism and special educational needs. Read about the emergency services experience <a href="http://www.essex-fire.gov.uk/news/Emergency_service_staff_become_autism_aware/#5b472fef62199" rel="noopener noreferrer">here</a>. Aston's ultimate dream is for this training to become mandatory for all emergency service workers.
                                <br/><br/>
                                Are you a school or group and interested in booking Aston's Way? Use the form below to submit a booking request. We'll reply back as soon as we can! Please note that there is generally a fee for this service, though sponsorship can be obtained if your
                                need is great. Applications are assessed on a case-by-case basis.
                            </Typography>
                            <hr/>
                            <Typography className={classes.formNote}>
                            Please note that hypertext links (http/https) are not permitted. This is to prevent spam.
                            </Typography>
                            <AstonsWayForm/>
                        </Paper>
                    </div>
                </Grid>
            </Grid>
        <br/>
        </div>
    )
}
export default AstonsWay;