import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
const useStyles = makeStyles(theme =>({
    heroContainer: {
    },
    heroTitle: {
        fontSize: '2rem',
        fontFamily: 'K2D, sans-serif',
        color: 'white',
    },
    heroText: {
        fontSize: '1.2rem',
        fontFamily: 'Ubuntu, sans-serif',
        color: 'white'
    },
}))

function AveryHero(props) {

    const classes=useStyles()
    return (
        <div>
            <br/>
            <Container maxWidth="sm" className={classes.heroContainer}>
                <Typography className={classes.heroTitle} align="center" color="textPrimary" gutterBottom>
                    {props.primary}
                </Typography>
                <Typography className={classes.heroText} align="center" color="textSecondary" paragraph>
                    {props.secondary}
                </Typography>
            </Container>
        </div>
    )
}

export default AveryHero;