import React, {useState} from 'react';
import {  makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Divider from '@material-ui/core/Divider'
import HomeIcon from '@material-ui/icons/Home';
import AirportShuttleIcon from '@material-ui/icons/AirportShuttle'
import EventIcon from '@material-ui/icons/Event';
import LocalMallIcon from '@material-ui/icons/LocalMall';
import GroupIcon from '@material-ui/icons/Group';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';
import ListItemText from '@material-ui/core/ListItemText';
import {Link} from "react-router-dom";
import Logo from './static/Home/logo.png'


const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
    },
    appBar: {
        background: `rgba(127,177,217,1)`,
        background: `linear-gradient(90deg, rgba(0,104,155,1) 0%, rgba(36,140,199,1) 35%, rgba(127,177,217,1) 100%)`, 
        postion: 'relative'
    },
    drawer: {
        background: `rgba(36,140,199,1)`,
        postion: 'relative',
        color: 'white',
    },
    menuButton: {
      marginRight: theme.spacing(2),
      
    },
    title: {
      flexGrow: 1,
      fontFamily: 'K2D, sans-serif'
    },
    button: {
        fontFamily: 'K2D, sans-serif',
        marginLeft: theme.spacing(2),
    },
    link: {
        textDecoration: 'none',
        color: 'white',
        fontFamily: 'K2D',
    },
    icon: {
        color: 'white'
    },
    box: {
        float: 'right'
    }
  }));

function AveryBar () {


    const sideList = side => (
        <div
          className={classes.list}
          role="presentation"
          onClick={toggleDrawer}
        >
          <List>
              <Link to="/" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <HomeIcon className={classes.icon} /></ListItemIcon>
                  <ListItemText primary="Home" />
                </ListItem>
              </Link>
              <Link to="/events" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <EventIcon className={classes.icon} /> </ListItemIcon>
                  <ListItemText primary="Events" />
                </ListItem>
              </Link>
              <Divider />
              <Link to="/pamper" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <LocalMallIcon className={classes.icon} /></ListItemIcon>
                  <ListItemText primary="Pamper Project" />
                </ListItem>
              </Link>
              <Link to="/thisisme" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <AccessibilityNewIcon className={classes.icon} /></ListItemIcon>
                  <ListItemText primary="This is Me" />
                </ListItem>
              </Link>
              <Link to="/astonsway" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <AirportShuttleIcon className={classes.icon}/></ListItemIcon> 
                  <ListItemText primary="Aston's Way"/>
                </ListItem>
              </Link>
              <Divider/>
              <Link to="/team" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <GroupIcon className={classes.icon} /></ListItemIcon>
                  <ListItemText primary="Meet the Team" />
                </ListItem>
              </Link>
              <Link to="/contact" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <ContactSupportIcon className={classes.icon} /></ListItemIcon>
                  <ListItemText primary="Contact Us" />
                </ListItem>
              </Link>
          </List>
          <Divider />
            <a href="https://www.facebook.com/teamaveryessex" className={classes.link} target="_blank" rel="noopener noreferrer">
            <ListItem button>
                <ListItemIcon> <FacebookIcon className={classes.icon} /></ListItemIcon>
                <ListItemText primary="Facebook" />
              </ListItem>
            </a>
            <a href="https://www.twitter.com/teamaveryessex" className={classes.link} target="_blank" rel="noopener noreferrer">
              <ListItem button>
                <ListItemIcon> <TwitterIcon className={classes.icon} /></ListItemIcon>
                <ListItemText primary="Twitter" />
              </ListItem>
            </a>
        </div>
    );
    const classes = useStyles();
    const [drawer, setDrawer] = useState(false)

    function toggleDrawer() {
        setDrawer(!drawer)
    }

    return (
        <div className={classes.root}>
            <AppBar position="static"  className={classes.appBar}>
                <Toolbar>
                <IconButton onClick={toggleDrawer}  edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                    Team Avery Essex
                </Typography>
                </Toolbar>
            </AppBar>
            <SwipeableDrawer  classes={{paper: classes.drawer}} disableDiscovery="true" disableSwipeToOpen="true" open={drawer} onClose={toggleDrawer} anchor="left">
                {sideList('left')}
            </SwipeableDrawer>
    </div>
    )
}

export default AveryBar;