import React from 'react';
import {makeStyles} from '@material-ui/core/styles'
import { Paper, Grid, Typography, CardMedia, Card, CardContent} from '@material-ui/core';
import {useMediaQuery} from 'react-responsive'
import {Link} from 'react-router-dom'
import AveryHero from './AveryHero'
import ThisIsMeForm from './forms/ThisIsMeForm'
import Presentation from './static/ThisIsMe/presentation.jfif'
import Chrissy from './static/ThisIsMe/chrissy.jfif'


const useStyles = makeStyles(theme => ({
    root: {

    },
    pamperText: {
        fontFamily: 'Rubik',
        fontSize: '1.2rem',
        margin: '0px 20px 0px 20px'
    },
    card: {
        boxShadow: '0 6px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
    },
    cardMedia: {
    },
    cardCaption : {
        fontSize: '1.1rem',
        textAlign: 'center',
        fontFamily: 'Ubuntu',
    },
    formNote: { 
        textAlign: 'center',
        margin: '0px 20px 0px 20px',
        fontFamily: 'Ubuntu',
    }
}))

function ThisIsMe() {
    const classes = useStyles();

    const isLarge = useMediaQuery({query: "(min-width: 1200px)"})
    const isSmall = useMediaQuery({query: "(min-width: 400px) and (max-width: 600px)"})
    let gridSize = 5
    let cardHeight = undefined
    if (isLarge) {gridSize = 4}
    if (isSmall) {cardHeight = 350}

    return (
        <div>
            <AveryHero
            primary="This is Me"
            secondary="An inspirational short film which has touched the hearts of many."
            />
            <Grid 
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <Grid item xs={10}>
                    <div data-aos="fade-up">
                        <Grid
                        container
                        spacing={3}
                        alignItems="stretch"
                        justify="center"
                        >
                            <Grid item sm={gridSize}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="Aston on the Chrissy B Show"
                                    height={cardHeight}
                                    image={Chrissy}
                                    title="Aston on the Chrissy B Show"
                                    /> 
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            Aston on the Chrissy B Show.
                                        </Typography>
                                    </CardContent>
                                </Card> 
                            </Grid>
                            <Grid item sm={gridSize}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="Aston presenting This is Me"
                                    height={cardHeight}
                                    image={Presentation}
                                    title="Aston presenting This is Me"
                                    />
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            Aston presenting This is Me.
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        </Grid>
                        <br/>
                        <Paper>
                        <br/>
                            <Typography className={classes.pamperText}>
                                An inspirational journey through thick and thin, This is Me is a short film put together by Aston and his family. It began life as a music video, recorded in secret as a surprise to Aston, but when he saw it, he decided to supplement
                                it with content and turn it into a short film! Created to help raise autism awareness and spread a positive message, This is Me is a heartwarming experience that's touched the hearts of many.
                                The film has been shown to many schools in the local area. To read more about these showings, please visit the <Link to="/events">events page</Link>.
                                <br/><br/>
                                Are you a school or group and interested in booking a showing of This is Me? Use the form below to submit a booking request. We'll reply back as soon as we can! All showings are 100% free.
                            </Typography>
                            <hr/>
                            <Typography className={classes.formNote}>
                            Please note that hypertext links (http/https) are not permitted. This is to prevent spam.
                            </Typography>
                            <ThisIsMeForm/>
                        </Paper>
                    </div>
                </Grid>
            </Grid>
        <br/>
        </div>
    )
}
export default ThisIsMe;