import React from 'react';
import {makeStyles} from '@material-ui/core/styles'
import { Paper, Grid, Typography, CardMedia, Card, CardContent} from '@material-ui/core';
import {useMediaQuery} from 'react-responsive'
import ContactForm from './forms/ContactForm'
import AveryHero from './AveryHero'


const useStyles = makeStyles(theme => ({
    root: {

    },
    pamperText: {
        fontFamily: 'Rubik',
        fontSize: '1.2rem',
        margin: '0px 20px 0px 20px'
    },
    card: {
        boxShadow: '0 6px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
    },
    cardMedia: {
    },
    cardCaption : {
        fontSize: '1.1rem',
        textAlign: 'center',
        fontFamily: 'Ubuntu',
    },
    formNote: { 
        textAlign: 'center',
        margin: '0px 20px 0px 20px',
        fontFamily: 'Ubuntu',
    }
}))

function Contact() {
    const classes = useStyles();

    const isLarge = useMediaQuery({query: "(min-width: 1200px)"})
    const isSmall = useMediaQuery({query: "(min-width: 400px) and (max-width: 600px)"})
    let gridSize = 5
    let cardHeight = undefined
    if (isLarge) {gridSize = 4}
    if (isSmall) {cardHeight = 350}

    return (
        <div>
            <AveryHero
            primary="Contact Us"
            secondary="Use the below form to contact us, or reach out to us on social media!"
            />
            <Grid 
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <Grid item xs={10}>
                    <div data-aos="fade-up">
                        <br/>
                        <Paper>
                        <br/>
                            <Typography className={classes.pamperText}>
                                Have any questions or queries? Feel free to get in touch using the form below. We'll reply as soon as we can!
                            </Typography>
                            <hr/>
                            <Typography className={classes.formNote}>
                            Please note that hypertext links (http/https) are not permitted. This is to prevent spam.
                            </Typography>
                            <ContactForm/>
                        </Paper>
                    </div>
                </Grid>
            </Grid>
        <br/>
        </div>
    )
}
export default Contact;