import React, {useState} from 'react';
import {Container, TextField, CircularProgress, Button, Grid, Typography, makeStyles} from '@material-ui/core';
import axios from 'axios'


const useStyles = makeStyles(theme => ({
    

    submissionText: {
        fontSize: '1.2rem',
        fontFamily: 'Ubuntu',
        padding: '100px',
        textAlign: 'center'
    },
    helperText: {
        textAlign: 'center',
        fontWeight: 'bold',
        fontSize: '1.2rem'
    },
    bottom : {
        paddingBottom: '20px'
    }
}))

function ContactForm() {

    const classes = useStyles()
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [text, setText] = useState("")
    const [formState, setFormState] = useState("initial")


    const LinkCheck = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/
    let responseObj = null;

    function handleSubmit() {
        setFormState("loading")
        axios.post('/api/pamper', {
            name: name,
            email: email,
            text: text
        })
        .then((response) => {
            responseObj = response.data
            if (responseObj.status == "success") {
                setFormState("done")
            }
            else {
                setFormState("error")
            }
        }, (error) => {
            setFormState("error")
        })
    }



    if (formState == "loading") {
        return (
            <Grid
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <br/>
                <CircularProgress size={150} thickness={1.5}/>
                <Button onClick={() => setFormState("done")}>Debug</Button>
                <br/>
            </Grid>
        )
    }
    else if (formState == "error") {
        if (responseObj) {
            return (
                <div>
                    <hr/>
                    <Typography className={classes.submissionText}>Submission Error.{responseObj.info}</Typography>
                </div>
            )
        }
        else {
            return (
                <div>
                    <hr/>
                    <Typography className={classes.submissionText}>Submission Error. You may have made too many requests. Please try again later.</Typography>
                </div>
            )
        }
    }
    else if (formState == "done") {
        return (
            <Grid
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <br/>
                <Typography className={classes.submissionText}>Thank you for your submission! Your email has been sent to the team.</Typography>
                <br/>
            </Grid>
        )
    }
    else {
        return (
            <Container>
                <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="pamper-name"
                label="Your Name"
                name="pamper-name"
                error={LinkCheck.test(name)}
                value={name}
                onChange={e => setName(e.target.value)}
                />
                <br/>
                <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="email"
                label="Your email address"
                name="email"
                error={LinkCheck.test(email)}
                value={email}
                onChange={e => setEmail(e.target.value)}
                />
                <br/>
                <TextField
                variant="outlined"
                required
                fullWidth
                id="general-text"
                label="Message"
                placeholder="Your message here..."
                multiline
                rows={8}
                margin="normal"
                error={LinkCheck.test(text)}
                value={text}
                onChange={e => setText(e.target.value)}
                />
                <br/>
               <div className={classes.bottom}>
                {(name && email && text) ?  
                        <Button className={classes.submit} variant="contained" color="primary" onClick={() => handleSubmit()}>Submit Form</Button>
                        : <Button className={classes.submit} variant="contained" color="primary" disabled>Enter missing details to submit</Button>
                    }
               </div>
            </Container>
        )
    }
}
export default ContactForm;