import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import {Grid} from '@material-ui/core'
import {useMediaQuery} from 'react-responsive'
import Logo from './static/Home/logo.png'

import TeamCard from './TeamCard.js'
import Skydive from './static/Home/skydive.jpg'
import Educate from './static/Home/educate.jpg'
import Achieve from './static/Home/achieve.jfif'
const useStyles = makeStyles(theme =>({
    heroContainer: {
        marginTop: '15px'
    },
    heroTitle: {
        fontSize: '1.8rem',
        fontFamily: 'K2D, sans-serif',
        color: 'white',
        fontWeight: 'bold'
    },
    heroText: {
        fontSize: '1.15rem',
        fontFamily: 'Ubuntu, sans-serif',
        color: 'white',
    },
    button: {
        marginTop: '10px',
        backgroundColor: '#209cee',
        color: 'white',
        textDecoration: 'none'
    },
}))

function AveryHero() {

    const classes=useStyles()
    const hearts = useMediaQuery({query: "(min-width: 550px"})
    console.log(hearts)

    const isTablet = useMediaQuery({ query: '(max-device-width: 850px)'})
    const isLaptop = useMediaQuery({ query: '(min-device-width: 851px) and (max-device-width: 1449px'})
    let gridSize = 3
    let height = 250

    // Set grid size
    if (isTablet) {
        gridSize = 9
        height = 350
    }
    else if (isLaptop) {
        gridSize = 4
    }
    else {
        gridSize = 3
    }
    
    return (
        <div>
            <Container maxWidth="sm" className={classes.heroContainer}>
                <Typography data-aos="fade" className={classes.heroTitle} align="center" color="textPrimary" gutterBottom>
                {hearts && <img src={Logo} height="50" width="50"/>}
               &nbsp; We are Team Avery Essex. &nbsp;
                {hearts && <img src={Logo} height="50" width="50"/>}
                </Typography>
                <Typography data-aos-delay="1500" data-aos="fade-in-down" className={classes.heroText} align="center" color="textSecondary" paragraph>
                    We empower diversity across Essex through meaningful interactions and events within the community.
                </Typography>
                <div className={classes.heroButtons} data-aos="fade-up" data-aos-delay="2000">
                    <Typography className={classes.heroText} align="center">Here's some of what we do.</Typography>
                </div>
            </Container>
            <Grid
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <Grid item xs={10}>
                    <Grid
                    container
                    spacing={3}
                    alignItems="stretch"
                    justify="center"
                    >
                        <Grid item sm={gridSize}>
                            <TeamCard
                            name="We inspire."
                            image={Skydive}
                            imgHeight={height}
                            bio="Through Aston's achievements, we hope to inspire others in similar positions."
                            animation="fade-right"
                            button
                            buttonText="Meet the Team"
                            buttonLink="/team"
                            />
                        </Grid>
                        <Grid item sm={gridSize}>
                            <TeamCard
                            name="We educate."
                            image={Educate}
                            imgHeight={height}
                            animation="fade-up"
                            bio="We've been educating people on autism and special educational needs since 2015."
                            button
                            buttonText="This is Me"
                            buttonLink="/thisisme"
                            />
                        </Grid>
                        <Grid item sm={gridSize}>
                            <TeamCard
                            name="We achieve."
                            image={Achieve}
                            imgHeight={height}
                            animation="fade-left"
                            bio="We've achieved more than we ever thought possible, working with amazing people."
                            button
                            buttonText="Events"
                            buttonLink="/events"
                            />
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
        
    )
}

export default AveryHero;