import React from 'react';
import Grid from '@material-ui/core/Grid'
import Event from './Event'
import LazyLoad from 'react-lazy-load'
import {Link} from 'react-router-dom'

import Halloween from './static/events/halloween.jpg'

function UpcomingEvents() {

    return (
        <div>
            <Grid 
            container
            spacing={0}
            alignItems="center"
            justify="center"
            data-aos="fade-up"
            >
                <Grid item xs={10}>
                    <LazyLoad offsetVertical={300}>
                        <Event
                            title="Bowers Halloween Disco"
                            date="31st October 2020"
                            image={Halloween}
                            text={<div>We have booked the <a href="https://www.bowersandpitseafootballclub.com/a/the-clubhouse-20861.html">Bowers Football Club Hall</a> for a spooky event! 90% of the Halloween Disco's proceeds will be going to <a href="https://www.ladansefantastique.uk.com/" target="_blank" rel="noopener noreferrer">La Danse Fantastique</a>, a great local dance school for SEN and regular children, with the remaining 10% going to autism charity <a href="https://www.annakennedyonline.com/" target="_blank" rel="noopener noreferrer" >AnnaKennedyOnline</a>. Tickets are priced at Â£5 each. Bring your own nibbles! There is a licensed bar on site. We will be presenting a prize to the best adult costume, and the best child costume, so get creative (and spooky)! We will also be holding a raffle on the night. If you'd like to book tickets, please <Link to="/contact">click here</Link> to visit the contact page, where you can message us.</div>}
                            direction="row-reverse"
                        />
                    </LazyLoad>
                </Grid>
            </Grid>
        </div>
    )
}
export default UpcomingEvents;