import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AveryHero from './AveryHero';
import PastEvents from './PastEvents';
import UpcomingEvents from './UpcomingEvents';

const useStyles = makeStyles(theme =>({
    line: {
        width: '80%',
        color: 'white'
    }
}))
function Events() {
    const classes = useStyles();

  return (
    <div>
        <AveryHero 
            primary="Events"
            secondary="Take a look at our upcoming events, or browse through past ones!"
        />
        
        <AveryHero
            primary="Upcoming Events"
        />
        <Grid 
        container
        spacing={0}
        alignItems="center"
        justify="center"
        >   
            <UpcomingEvents/>
            <AveryHero
                primary="Past Events"
            />
            <PastEvents/>
        </Grid>
        <br/>

    </div>
 )}
export default Events;